.gallery {
  background-color: #403f3f;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  .title {
    padding: 4rem 0rem;
    color: white;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
    font-size: 2.5rem;
    font-weight: 600;
  }

  .slider {
    height: 60vh;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    .arrowButton {
      cursor: pointer;
      height: 1rem;
      width: 8%;
      background-image: url(./Arrow.png);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      background-color: #caae5f;
      border: #caae5f solid 15px;

      &:first-of-type {
        transform: rotate(180deg);
      }
    }

    .imageContainer {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50%;
      height: 100%;

      img {
        height: 100%;
      }
    }
  }

  .thumbnails {
    padding: 75px 0;
    display: flex;
    justify-content: center;
    background-color: transparent;
    width: 30%;

    img {
      width: 50px;
      height: 50px;
      margin: 0 5px;
      cursor: pointer;
      transition: opacity 0.3s;

      &.activeThumbnail {
        border: 2px solid #3498db;
        opacity: 1;
        transform: scale(1.1);
      }

      &:hover {
        transform: scale(1.2);
      }
    }
  }
}

//Tablets

@media screen and (min-width: 800px) and (max-width: 1023px) {
}

//Phones

@media screen and (max-width: 800px) {
  .gallery {
    .slider {
      height: 40vh;

      .arrowButton {
        height: 0.4rem;
        width: 5%;
      }

      .imageContainer {
        width: 70%;

        img {
          height: auto;
          width: 100%;
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center;
        }
      }
    }

    .thumbnails {
      padding: 75px 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: transparent;
      width: 80%;

      img {
        height: 100%;
      }
    }
  }
}
